import { autoinject } from "aurelia-framework";
import { DialogController } from "aurelia-dialog";
import { JobService } from "../../services/data/job-service";
import { SubJobOptionService } from "../../services/data/sub-job-service";
import { JobDocument, Job } from "../../models/job";
import { JobStatus } from "../../models/job-status";
import { JobType } from "../../models/job-type";
import { equals, startsWith, endsWith } from "../../utilities/equals";
import { guid } from "../../utilities/guid";
import { sortBy } from "../../utilities/sort";

const sortByValue = sortBy("value");

interface SelectableOption {
  value: string;
  name: string;
  checked: boolean;
  error?: string;
}

@autoinject()
export class SubJobs {
  options: SelectableOption[] = [];
  jobs: Job[] = [];
  job: JobDocument | null = null;
  isSubJob = false;

  constructor(
    private element: Element,
    private controller: DialogController,
    private jobService: JobService,
    private subJobService: SubJobOptionService
  ) {}

  async activate({ job }: { job: JobDocument }) {
    this.jobs = await this.jobService.getAll();
    const options = await this.subJobService.getAll();

    this.isSubJob = this.jobs.some(
      (j) =>
        startsWith(job.number, j.number) && job.number.length > j.number.length
    );

    this.options = options
      .filter(
        (o) =>
          this.isSubJob ||
          !this.jobs.some((j) => equals(j.number, `${job.number}${o.value}`))
      )
      .sort(sortByValue)
      .map((o) => ({
        name: o.name,
        value: this.isSubJob ? "1" : o.value,
        checked: false,
      }));
    this.job = job;
  }

  attached() {
    $(".ui.checkbox", this.element).checkbox();
  }

  detached() {
    $(".ui.checkbox").checkbox("destroy");
  }

  get job_prefix(): string {
    return this.job.job_type === JobType.PROJECT ? "P" : "S";
  }

  addOption() {
    this.options.push({ value: "", name: "", checked: true });

    window.setTimeout(() => {
      $(".ui.checkbox", this.element).checkbox("destroy").checkbox();
    }, 100);
  }

  validateOption(option: SelectableOption) {
    if (option.checked) {
      const duplicates = this.jobs.some((j) =>
        equals(j.number, `${this.job.number}${option.value}`)
      );
      option.error = duplicates ? "This job number already exists" : "";
      option.value = option.value.toUpperCase();
    }
  }

  optionJobNumber(value: string) {
    return `${this.job.number}${value.toUpperCase()}`;
  }

  optionJobName(name: string) {
    return this.job.name + (endsWith(this.job.name, name) ? "" : `: ${name}`);
  }

  save() {
    if (this.job && !this.options.some((o) => o.error)) {
      const selectedOptions = this.options.filter((o) => o.checked && o.value);

      const duplicates = selectedOptions.filter((o) =>
        this.jobs.some((j) => equals(j.number, `${this.job.number}${o.value}`))
      );

      if (!duplicates.length) {
        selectedOptions.forEach((o) => {
          const document = new JobDocument(this.job),
            json = document.toJSON();

          json._id = guid();
          delete json._rev;
          json.number = this.optionJobNumber(o.value);
          json.status = JobStatus.PENDING;
          json.name = this.optionJobName(o.name);

          this.jobService
            .save(json)
            .then((result) => {
              console.log(result);
            })
            .catch((e) => {
              console.log(e);
            });

          this.controller.close(true);
        });
      }
    }
  }
}
