import { autoinject } from "aurelia-framework";
import { EventAggregator, Subscription } from "aurelia-event-aggregator";
import { RouterEvent } from "aurelia-router";
import { Authentication, Roles } from "../../services/auth";
import { log } from "../../services/log";
import { Database } from "../../services/data/db";
import { BillingType } from "../../models/billing-type";
import { Job } from "../../models/job";
import { JobType } from "../../models/job-type";
import { JobStatus } from "../../models/job-status";
import { JobService } from "../../services/data/job-service";
import { UserService } from "../../services/data/users-service";
import { CloseJobArgs } from "./close-job";
import { JobListFilters } from "./job-list-filters";
import { equals, contains } from "../../utilities/equals";
import { sort } from "../../utilities/sort";
import { ListJobs } from "./list-jobs";

@autoinject()
export class JobList {
  items: Job[];
  filteredItems: Job[];
  foremen: string[];
  billingTypes: BillingType[] = BillingType.OPTIONS;
  myJobs = false;
  showOpen = true;
  showClosed = false;
  showCompleted = false;
  reverseSort = false;
  customerSort = false;
  showProjects = true;
  showServiceCalls = true;
  showTimeMaterialJobs = true;
  showContractJobs = true;
  billingTypeFilter = "All";
  search = "";
  filtersExpanded = false;
  closeJobArgs = new CloseJobArgs();
  showModalSubscription: Subscription;
  syncChangeSubscription: Subscription;
  routerSubscription: Subscription;
  filters: JobListFilters;

  constructor(
    private element: Element,
    private auth: Authentication,
    private jobService: JobService,
    private events: EventAggregator,
    private userService: UserService,
    // https://ilikekillnerds.com/2016/02/shared-state-in-aurelia
    private context: ListJobs
  ) {
    this.showCompleted =
      auth.isInRole(Roles.OfficeAdmin) || auth.isInRole(Roles.Administrator);
    this.myJobs =
      !auth.isInRole(Roles.ViewAllJobs) &&
      !auth.isInRole(Roles.EditAllJobs) &&
      !auth.isInRole(Roles.OfficeAdmin) &&
      !auth.isInRole(Roles.Administrator);
    this.filters = JobListFilters.load(this);
    Object.assign(this, this.filters);
  }

  created() {
    if (this.context.jobs) {
      this.items = this.context.jobs;
      this.filteredItems = this.context.filteredItems;
      this.foremen = this.context.foremen;
    }
  }

  async attached() {
    window.scrollTo(0, this.context.scrollTop);

    const that = this;
    $(".modal.close-job", this.element).modal({
      onApprove: () => {
        this.events.publish(CloseJobArgs.ModalApprovedEvent, that.closeJobArgs);
      },
    });

    $(".ui.toggle.checkbox", this.element).checkbox({
      onChange: this.filter.bind(this),
    });
    $(".ui.dropdown.billing-type", this.element).dropdown({
      onChange: this.filter.bind(this),
    });

    this.routerSubscription = this.events.subscribe(
      RouterEvent.Processing,
      () => {
        console.log("processing router event");
        this.context.scrollTop = window.scrollY;
      }
    );

    this.showModalSubscription = this.events.subscribe(
      CloseJobArgs.ShowModalEvent,
      this.showCloseJobModal.bind(this)
    );
    this.syncChangeSubscription = this.events.subscribe(
      Database.SyncChangeEvent,
      this.refresh.bind(this)
    );

    const foremen = await this.userService.getAllInRole(Roles.Foreman);
    this.foremen = foremen.map(({ name }) => name);

    if (!this.context.jobs) {
      this.refresh();
    }
  }

  detached() {
    $(".modal.close-job", this.element).modal("destroy");
    $(".ui.dropdown.billing-type", this.element).dropdown("destroy");

    this.showModalSubscription.dispose();
    this.routerSubscription.dispose();

    this.context.jobs = this.items;
    this.context.filteredItems = this.filteredItems;
    this.context.foremen = this.foremen;
  }

  refresh() {
    this.jobService.getAll().then((items) => {
      this.items = items;
      this.filter();
    });
  }

  filter() {
    const me = this.auth.userInfo().name;

    const search = (i: Job) =>
      !this.search ||
      contains(i.customer.name, this.search) ||
      contains(i.name, this.search) ||
      contains(i.number, this.search);
    const mine = (i) => {
      return (
        !this.myJobs ||
        (Array.isArray(i.foreman)
          ? i.foreman.indexOf(me) !== -1
          : equals(i.foreman, me))
      );
    };
    const open = (i) =>
      this.showOpen &&
      (equals(i.status, JobStatus.PENDING) ||
        equals(i.status, JobStatus.IN_PROGRESS));
    const completed = (i) =>
      this.showCompleted && equals(i.status, JobStatus.COMPLETE);
    const closed = (i) => this.showClosed && equals(i.status, JobStatus.CLOSED);
    const projects = (i) =>
      this.showProjects && equals(i.job_type, JobType.PROJECT);
    const serviceCalls = (i) =>
      this.showServiceCalls && equals(i.job_type, JobType.SERVICE_CALL);
    const billingType = (i) =>
      equals(this.billingTypeFilter, "all") ||
      equals(i.billing_type, this.billingTypeFilter);

    log.debug(`Only show my jobs: ${this.myJobs}`);
    log.debug(`Show open jobs: ${this.showOpen}`);
    log.debug(`Show completed jobs: ${this.showCompleted}`);
    log.debug(`Show closed jobs: ${this.showClosed}`);
    log.debug(`Show projects: ${this.showProjects}`);
    log.debug(`Show service calls: ${this.showServiceCalls}`);
    log.debug(`Show time and materials jobs: ${this.showTimeMaterialJobs}`);
    log.debug(`Show contract jobs: ${this.showContractJobs}`);
    log.debug(`Billing type filter: ${this.billingTypeFilter}`);

    let sortedItems = this.items.filter(
      (i) =>
        search(i) &&
        mine(i) &&
        (open(i) || closed(i) || completed(i)) &&
        (projects(i) || serviceCalls(i)) &&
        billingType(i)
    );
    sortedItems.sort((a, b) => {
      const direction = this.reverseSort ? "descending" : "",
        customerResult =
          this.customerSort &&
          sort(a.customer.name, b.customer.name, direction),
        numberWithPrefixSort = sort(a.number, b.number, direction);

      return customerResult || numberWithPrefixSort;
    });

    this.filteredItems = sortedItems;

    this.filters.save(this);
  }

  toggleFiltersExpanded() {
    this.filtersExpanded = !this.filtersExpanded;
  }

  showCloseJobModal(id: string) {
    this.closeJobArgs.jobId = id;
    $(".modal.close-job").modal("show");
  }

  get canEditAllJobs() {
    return (
      this.auth.isInRole(Roles.EditAllJobs) ||
      this.auth.isInRole(Roles.Administrator)
    );
  }

  get canViewAllJobs() {
    return (
      this.auth.isInRole(Roles.ViewAllJobs) ||
      this.auth.isInRole(Roles.Administrator)
    );
  }

  get isOfficeAdmin() {
    return (
      this.auth.isInRole(Roles.OfficeAdmin) ||
      this.auth.isInRole(Roles.Administrator)
    );
  }
}
